.player-answer-notifications {
    position: fixed;
    top: 100px;
    right: 30px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 10px;
    pointer-events: none;
}

.player-answer-notification {
    background: rgba(0, 0, 0, 0.4);
    color: white;
    padding: 12px 20px;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 16px;
    font-weight: 600;
    animation: fadeInOut 2.5s ease-in-out forwards;
    min-width: 200px;
}

.player-answer-notification .player-name {
    color: #fff;
    font-weight: 700;
}

.player-answer-notification .answered-text {
    color: rgba(255, 255, 255, 0.9);
    font-weight: 500;
}

@keyframes fadeInOut {
    0% {
        opacity: 0;
        transform: translateX(50px) scale(0.9);
    }

    15% {
        opacity: 1;
        transform: translateX(0) scale(1);
    }

    85% {
        opacity: 1;
        transform: translateX(0) scale(1);
    }

    100% {
        opacity: 0;
        transform: translateX(50px) scale(0.9);
    }
}