.pick-order-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 2rem;
    box-sizing: border-box;
    color: white;
}

.export-btn {
    position: absolute;
    top: 1rem;
    right: 1rem;
    padding: 0.5rem 1rem;
    background-color: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.4);
    color: white;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.9rem;
    transition: all 0.2s ease;
    z-index: 100;
}

.export-btn:hover {
    background-color: rgba(255, 255, 255, 0.3);
}

.pick-order-title {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    text-align: center;
}

.pick-order-status {
    font-size: 1.5rem;
    margin-bottom: 2rem;
    color: #aaa;
}

.upcoming-list-container {
    display: flex;
    flex-wrap: wrap;
    /* allow wrap if very small screen, though we limit to 5 */
    justify-content: center;
    align-items: center;
    gap: 1rem;
    background-color: rgba(255, 255, 255, 0.1);
    padding: 0.8rem 1.5rem;
    border-radius: 8px;
    margin-bottom: 1.5rem;
    border: 1px solid rgba(255, 255, 255, 0.2);
    min-height: 50px;
}

.upcoming-label {
    font-weight: bold;
    color: #F8B229;
    margin-right: 0.5rem;
    text-transform: uppercase;
    font-size: 0.9rem;
    letter-spacing: 0.05em;
}

.upcoming-list-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.upcoming-name {
    font-size: 1.1rem;
    font-weight: 500;
}

.upcoming-arrow {
    color: rgba(255, 255, 255, 0.4);
    font-size: 1rem;
}

.upcoming-more {
    color: rgba(255, 255, 255, 0.5);
    font-style: italic;
    margin-left: 0.5rem;
}

/* HOST VIEW - GRID */
.host-view .pick-order-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 1.5rem;
    width: 100%;
    max-width: 1200px;
    max-height: 80vh;
    overflow-y: auto;
    padding: 1rem;
}

.pick-grid-item {
    background: rgba(255, 255, 255, 0.1);
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    padding: 0.3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 90px;
    transition: all 0.3s ease;
}

.pick-grid-item.available {
    background: rgba(255, 255, 255, 0.1);
}

.pick-grid-item.taken {
    background: linear-gradient(135deg, #D42426, #F8B229);
    border-color: transparent;
    transform: scale(1.05);
    box-shadow: 0 0 15px rgba(248, 178, 41, 0.5);
}

.pick-grid-item .pick-number {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

.pick-grid-item .pick-name {
    font-size: 1.2rem;
    text-align: center;
    font-weight: 400;
    word-break: break-word;
    padding-bottom: 10px
}

/* CLIENT VIEW - LIST */
.client-view {
    padding: 1rem;
    height: 100vh;
    overflow: hidden;
}

.turn-banner {
    width: 100%;
    padding: 1rem;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
    border-radius: 8px;
    margin-bottom: 1rem;
}

.turn-banner.your-turn {
    background-color: #F8B229;
    color: #000;
    animation: pulse 1s infinite alternate;
}

.turn-banner.waiting {
    background-color: rgba(255, 255, 255, 0.1);
    color: #aaa;
}

.pick-list-scroll {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
    max-width: 500px;
    overflow-y: auto;
    padding-bottom: 5rem;
    /* Space for bottom nav or just clearance */
    flex: 1;
}

.pick-list-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.2rem;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    color: white;
    font-size: 1.1rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.pick-list-item.selectable:hover {
    background: rgba(255, 255, 255, 0.25);
    transform: translateY(-2px);
}

.pick-list-item.taken {
    opacity: 0.6;
    background: rgba(0, 0, 0, 0.3);
    cursor: not-allowed;
    border-color: transparent;
}

.pick-list-item:disabled {
    cursor: not-allowed;
}

.pick-list-item .pick-number {
    font-size: 1.5rem;
    font-weight: bold;
    min-width: 40px;
}

.pick-list-item .pick-status {
    color: #F8B229;
}

.pick-list-item .pick-taker {
    color: #aaa;
    font-style: italic;
}

@keyframes pulse {
    from {
        opacity: 0.8;
        transform: scale(1);
    }

    to {
        opacity: 1;
        transform: scale(1.02);
    }
}