.client-view {
  min-height: 100vh;
  background: linear-gradient(135deg, #D42426 0%, #FF6B6B 100%);
  color: white;
  display: flex;
  flex-direction: column;
}

.client-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 40px;
  text-align: center;
}

.client-container h1 {
  font-size: 3rem;
  margin: 0 0 40px 0;
}

.join-form {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 100%;
}

.form-group {
  margin-bottom: 25px;
  text-align: left;
}

.form-group label {
  display: block;
  color: #333;
  font-weight: 600;
  margin-bottom: 8px;
  font-size: 0.9rem;
}

.form-input {
  width: 100%;
  padding: 15px;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-sizing: border-box;
}

.form-input:focus {
  outline: none;
  border-color: #D42426;
  box-shadow: 0 0 0 3px rgba(212, 36, 38, 0.1);
}

.join-button {
  width: 100%;
  padding: 15px;
  background: linear-gradient(135deg, #D42426 0%, #FF6B6B 100%);
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 15px;
}

.join-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(212, 36, 38, 0.4);
}

.join-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.back-button {
  width: 100%;
  padding: 12px;
  background: transparent;
  color: #666;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-button:hover {
  border-color: #999;
  background: #f5f5f5;
}

.client-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.client-info {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.player-name {
  font-size: 1.3rem;
  font-weight: 700;
}

.room-code {
  font-size: 0.9rem;
  opacity: 0.8;
  font-family: 'Courier New', monospace;
}

.player-score {
  font-size: 1.1rem;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.2);
  padding: 5px 15px;
  border-radius: 20px;
  display: inline-block;
  margin-top: 5px;
}

.admin-toggle-button {
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.admin-toggle-button:hover {
  background: rgba(255, 255, 255, 0.3);
}

.password-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.password-modal-content {
  background: white;
  padding: 40px;
  border-radius: 20px;
  max-width: 400px;
  width: 90%;
  text-align: center;
}

.password-modal-content h3 {
  color: #333;
  margin: 0 0 20px 0;
  font-size: 1.5rem;
}

.password-modal-content .form-input {
  margin-bottom: 20px;
}

.password-modal-buttons {
  display: flex;
  gap: 10px;
}

.submit-button,
.cancel-button {
  flex: 1;
  padding: 12px;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-button {
  background: linear-gradient(135deg, #D42426 0%, #FF6B6B 100%);
  color: white;
}

.submit-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(212, 36, 38, 0.4);
}

.cancel-button {
  background: #e0e0e0;
  color: #333;
}

.cancel-button:hover {
  background: #d0d0d0;
}

.client-content {
  flex: 1;
  display: flex;
  align-items: flex-start;
  /* Changed from center to allow scrolling */
  justify-content: center;
  padding: 40px 20px;
  width: 100%;
  overflow-y: auto;
  /* Enable scrolling */
}

.waiting-screen {
  text-align: center;
  margin-top: auto;
  /* Vertically center only waiting screen */
  margin-bottom: auto;
}

.waiting-screen h2 {
  font-size: 2rem;
  margin: 0 0 15px 0;
}

.waiting-screen p {
  font-size: 1.1rem;
  opacity: 0.9;
}

.question-screen {
  width: 100%;
  max-width: 800px;
  text-align: center;
}

.answer-button {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 25px;
  background: rgba(255, 255, 255, 0.15);
  color: white;
  border: 3px solid transparent;
  border-radius: 15px;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  backdrop-filter: blur(10px);
}

.answer-button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
}

.answer-button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

.answer-button.selected {
  background: rgba(76, 175, 80, 0.4);
  border-color: rgba(76, 175, 80, 0.8);
  font-weight: 700;
}

.answer-button.correct {
  background: rgba(76, 175, 80, 0.8);
  border-color: #ffffff;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.answer-button.incorrect {
  background: rgba(244, 67, 54, 0.8);
  border-color: #ffffff;
}

.answer-letter {
  font-size: 1.5rem;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.3);
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  flex-shrink: 0;
}

.answer-text {
  flex: 1;
}

.answer-confirmation {
  background: rgba(76, 175, 80, 0.3);
  padding: 15px 30px;
  border-radius: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  display: inline-block;
}

.results-screen {
  text-align: center;
  max-width: 600px;
}

.results-screen h2 {
  font-size: 2.5rem;
  margin: 0 0 30px 0;
}

.your-answer {
  background: rgba(255, 255, 255, 0.2);
  padding: 20px;
  border-radius: 15px;
  font-size: 1.3rem;
  margin: 30px 0;
  font-weight: 600;
}

/* Question Display Specific Styles */
.question-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 800px;
}

.question-header {
  margin-bottom: 30px;
  text-align: center;
  width: 100%;
}

.question-text {
  font-size: 2rem;
  font-weight: 700;
  margin: 0 0 15px 0;
  line-height: 1.3;
}

.question-meta {
  display: flex;
  justify-content: center;
  gap: 20px;
  font-size: 1.1rem;
  opacity: 0.9;
}

.answers-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  width: 100%;
}

.results-message {
  margin-top: 30px;
  text-align: center;
  padding: 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  width: 100%;
}

.correct-msg {
  color: #ffffff;
  font-size: 1.8rem;
  margin: 0;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

.incorrect-msg {
  color: #ffffff;
  font-size: 1.8rem;
  margin: 0;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

/* Mobile Responsive Styles */
@media (max-width: 768px) {
  .client-container {
    padding: 20px;
  }

  .client-container h1 {
    font-size: 2rem;
    margin-bottom: 25px;
  }

  .join-form {
    padding: 25px;
  }

  .client-header {
    padding: 15px 20px;
    flex-direction: row;
    /* Keep row but adjust size */
  }

  .player-name {
    font-size: 1.1rem;
  }

  .room-code {
    font-size: 0.8rem;
  }

  .player-score {
    font-size: 0.9rem;
    padding: 3px 10px;
  }

  .admin-toggle-button {
    padding: 8px 15px;
    font-size: 0.8rem;
  }

  .client-content {
    padding: 20px 15px;
  }

  /* Question Display Mobile */
  .question-text {
    font-size: 1.5rem;
  }

  .answers-grid {
    grid-template-columns: 1fr;
    /* Stack answers on mobile */
  }

  .answer-button {
    padding: 15px 20px;
    font-size: 1.1rem;
  }

  .waiting-screen h2 {
    font-size: 1.5rem;
  }
}

/* Checkbox Group */
.checkbox-group {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* Align with input label */
  padding-left: 5px;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
  font-size: 1rem;
  color: #333;
  user-select: none;
}

.form-checkbox {
  width: 22px;
  height: 22px;
  cursor: pointer;
  accent-color: #D42426;
  margin: 0;
}

/* Header Buttons */
.header-buttons {
  display: flex;
  gap: 10px;
  align-items: center;
}

.leave-button {
  padding: 8px 15px;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.leave-button:hover {
  background: rgba(244, 67, 54, 0.4);
  border-color: rgba(244, 67, 54, 0.6);
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  backdrop-filter: blur(5px);
}

.modal-content {
  background: white;
  color: #333;
  padding: 30px;
  border-radius: 20px;
  max-width: 400px;
  width: 90%;
  text-align: center;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.4);
  animation: modalFadeIn 0.3s ease;
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-content h3 {
  margin-top: 0;
  color: #D42426;
  font-size: 1.6rem;
  margin-bottom: 15px;
}

.modal-content p {
  margin-bottom: 25px;
  font-size: 1.1rem;
  line-height: 1.5;
  color: #555;
}

.modal-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.confirm-button {
  padding: 12px 25px;
  background: #D42426;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
}

.confirm-button:hover {
  background: #b71c1c;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(212, 36, 38, 0.3);
}