.host-view {
  min-height: 100vh;
  background: linear-gradient(135deg, #165B33 0%, #2E8B57 100%);
  color: white;
  display: flex;
  flex-direction: column;
}

.host-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 40px;
  text-align: center;
}

.host-container h1 {
  font-size: 3rem;
  margin: 0 0 20px 0;
}

.host-description {
  font-size: 1.2rem;
  margin: 0 0 40px 0;
  opacity: 0.9;
}

.create-room-button {
  padding: 20px 50px;
  font-size: 1.3rem;
  background: white;
  color: #165B33;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.create-room-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.host-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 40px;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.room-code-display {
  display: flex;
  align-items: center;
  gap: 15px;
}

.room-code-label {
  font-size: 1.2rem;
  opacity: 0.9;
}

.room-code-value {
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 5px;
  background: rgba(255, 255, 255, 0.2);
  padding: 10px 20px;
  border-radius: 10px;
  font-family: 'Courier New', monospace;
}

.game-state-badge {
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.9rem;
  letter-spacing: 1px;
}

.question-counter {
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  font-weight: 600;
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-family: 'Courier New', monospace;
}

.host-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 40px 40px 40px;
  margin-bottom: 90px;
}

.waiting-screen {
  text-align: center;
}

.waiting-screen h2 {
  font-size: 2.5rem;
  margin: 0 0 20px 0;
}

.waiting-screen p {
  font-size: 1.3rem;
  opacity: 0.9;
  margin: 0 0 30px 0;
}

.players-list {
  margin: 40px 0;
  max-width: 800px;
  width: 100%;
}

.players-list h3 {
  font-size: 1.5rem;
  margin: 0 0 20px 0;
  opacity: 0.9;
}

.players-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 15px;
  margin-bottom: 30px;
}

.player-card {
  background: rgba(255, 255, 255, 0.15);
  padding: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.player-card:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
}

.player-name {
  font-size: 1.1rem;
  font-weight: 600;
}

.player-badge {
  font-size: 0.8rem;
  padding: 4px 10px;
  border-radius: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.admin-badge {
  background: rgba(255, 193, 7, 0.3);
  color: #ffc107;
}

.no-players {
  font-size: 1rem;
  opacity: 0.7;
  font-style: italic;
  margin: 20px 0;
}

.start-question-button {
  padding: 20px 50px;
  font-size: 1.2rem;
  background: white;
  color: #165B33;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.start-question-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.question-screen {
  width: 100%;
  max-width: 1200px;
  text-align: center;
}

.timer-display {
  margin-bottom: 40px;
}

.timer {
  display: inline-block;
  font-size: 4rem;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.2);
  padding: 20px 40px;
  border-radius: 20px;
  min-width: 150px;
}

.timer-warning {
  color: #ff6b6b;
  animation: pulse 1s infinite;
}

@keyframes pulse {

  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }
}

.question-text {
  font-size: 3rem;
  font-weight: 700;
  margin: 0 0 60px 0;
}

.question-meta {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 30px;
  font-size: 2rem;
  opacity: 0.9;
}

.question-meta .timer {
  font-size: 1.2rem;
  padding: 0;
  background: none;
  min-width: auto;
  font-weight: normal;
}

/* QuestionDisplay styles */
.answers-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-bottom: 40px;
}

.answer-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  background: rgba(255, 255, 255, 0.15);
  padding: 30px;
  border-radius: 15px;
  font-size: 1.5rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  border: 3px solid transparent;
  color: white;
  cursor: default;
  /* Host doesn't click */
  width: 100%;
}

.answer-button.correct {
  background: rgba(76, 175, 80, 0.6);
  border-color: #ffffff;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.answer-button.incorrect {
  background: rgba(244, 67, 54, 0.6);
  border-color: #ffffff;
}

.answer-button.selected {
  background: rgba(255, 255, 255, 0.3);
  border-color: white;
}

.answer-button.disabled {
  /* Host buttons are disabled but should still be visible */
  opacity: 1;
  cursor: default;
}

.answer-text {
  flex: 1;
  text-align: left;
}

.waiting-answers {
  font-size: 1.3rem;
  opacity: 0.8;
  margin-top: 40px;
}

.results-screen {
  text-align: center;
  max-width: 800px;
}

.results-screen h2 {
  font-size: 3rem;
  margin: 0 0 40px 0;
}

.correct-answer {
  font-size: 2rem;
  background: rgba(76, 175, 80, 0.3);
  padding: 30px;
  border-radius: 15px;
  margin: 40px 0;
  font-weight: 600;
}

.next-question-button {
  padding: 20px 50px;
  font-size: 1.2rem;
  background: white;
  color: #165B33;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  margin-top: 40px;
}

.next-question-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}