.pachinko-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    min-height: 100vh;
}

.pachinko-title {
    font-size: 2.5rem;
    color: white;
    margin-bottom: 30px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.pachinko-intro,
.pachinko-waiting {
    text-align: center;
    color: white;
    font-size: 1.5rem;
}

.pachinko-intro p,
.pachinko-waiting p {
    margin: 15px 0;
}

.pachinko-instructions {
    font-size: 1.2rem;
    opacity: 0.9;
}

.pachinko-board {
    background: white;
    border-radius: 15px;
    padding: 20px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
}

.pachinko-canvas {
    display: block;
    border: 3px solid #2c3e50;
    border-radius: 10px;
    background: linear-gradient(180deg, #ecf0f1 0%, #bdc3c7 100%);
}