.number-picker-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    max-width: 700px;
    margin: 0 auto;
}

.number-picker-display.host-view {
    max-width: 100%;
    width: 100%;
}

.number-picker-header {
    text-align: center;
    margin-bottom: 2rem;
    width: 100%;
}

.number-picker-text {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #ffffff;
}

.number-picker-meta {
    display: flex;
    justify-content: center;
    gap: 2rem;
    font-size: 1.1rem;
    color: #ffffff;
    flex-wrap: wrap;
    text-align: center;
}

.number-picker-controls {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.current-value-display {
    text-align: center;
    padding: 1rem;
    background-color: #e3f2fd;
    border-radius: 8px;
}

.current-value-display h3 {
    margin: 0;
    font-size: 1.8rem;
    color: #165B33;
}

.slider-container {
    display: flex;
    align-items: center;
    gap: 1rem;
    width: 100%;
}

.range-label {
    font-size: 1.1rem;
    font-weight: bold;
    color: #ffffff;
    min-width: 50px;
    text-align: center;
}

.number-slider {
    flex: 1;
    height: 8px;
    border-radius: 5px;
    background: #ddd;
    outline: none;
    -webkit-appearance: none;
}

.number-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
    transition: all 0.3s ease;
}

.number-slider::-webkit-slider-thumb:hover {
    background: #45a049;
    transform: scale(1.2);
}

.number-slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background: #4CAF50;
    cursor: pointer;
    border: none;
    transition: all 0.3s ease;
}

.number-slider::-moz-range-thumb:hover {
    background: #45a049;
    transform: scale(1.2);
}

.input-container {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.input-container label {
    font-size: 1.1rem;
    color: #ffffff;
    text-align: center;
}

.number-input {
    padding: 0.8rem;
    font-size: 1.2rem;
    border: 2px solid #ddd;
    border-radius: 8px;
    text-align: center;
}

.number-input:focus {
    outline: none;
    border-color: #4CAF50;
}

.submit-number-button {
    padding: 1rem 2rem;
    font-size: 1.2rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.submit-number-button:hover {
    background-color: #45a049;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.number-picker-results {
    width: 100%;
    margin-top: 2rem;
}

.answer-comparison {
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 1.5rem;
    flex-wrap: wrap;
}

.answer-box {
    padding: 1.5rem;
    background-color: #f5f5f5;
    border-radius: 8px;
    text-align: center;
    min-width: 150px;
    border: 2px solid #ddd;
}

.answer-box.correct {
    background-color: #e8f5e9;
    border-color: #4CAF50;
}

.answer-box h4 {
    margin: 0 0 0.5rem 0;
    color: #555;
    font-size: 1rem;
}

.answer-value {
    margin: 0;
    font-size: 2rem;
    font-weight: bold;
    color: #333;
}

.accuracy-display {
    text-align: center;
    padding: 1rem;
    background-color: #e3f2fd;
    border-radius: 8px;
}

.accuracy-display p {
    margin: 0;
    font-size: 1.3rem;
    color: #165B33;
    font-weight: bold;
}

.host-results {
    text-align: center;
    padding: 1.5rem;
    background-color: #e8f5e9;
    border-radius: 8px;
    margin-top: 2rem;
}

.host-results h3 {
    margin: 0;
    color: #4CAF50;
    font-size: 1.5rem;
}

@media (max-width: 768px) {
    .number-picker-display {
        padding: 1rem;
    }

    .number-picker-text {
        font-size: 1.5rem;
    }

    .current-value-display h3 {
        font-size: 1.5rem;
    }

    .answer-comparison {
        flex-direction: column;
        gap: 1rem;
    }

    .answer-box {
        min-width: unset;
        width: 100%;
    }

    .submit-number-button {
        padding: 0.8rem 1.5rem;
        font-size: 1.1rem;
    }
}