.color-question-display {
    width: 100%;
    max-width: 900px;
    margin: 0 auto;
    padding: 2rem;
}

.color-question-header {
    text-align: center;
    margin-bottom: 2rem;
}

.color-question-text {
    font-size: 2rem;
    font-weight: 700;
    color: #ffffff;
    margin-bottom: 1rem;
}

.color-question-meta {
    display: flex;
    justify-content: center;
    gap: 2rem;
    font-size: 1.2rem;
    color: #ffffff;
}

.color-question-meta .timer {
    font-weight: 700;
    color: #4CAF50;
}

.color-display-section {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 3rem;
    margin: 2rem 0;
    flex-wrap: wrap;
}

.target-color-container,
.color-picker-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.target-color-container h3,
.color-picker-container h3 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
    margin: 0;
}

.color-box {
    width: 200px;
    height: 200px;
    border-radius: 16px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
    border: 4px solid #ffffff;
    transition: transform 0.2s ease;
}

.color-box:hover {
    transform: scale(1.05);
}

.target-color {
    animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {

    0%,
    100% {
        box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
    }

    50% {
        box-shadow: 0 8px 32px rgba(255, 255, 255, 0.4);
    }
}

.submit-color-button {
    padding: 1rem 2rem;
    font-size: 1.2rem;
    font-weight: 600;
    color: #ffffff;
    background: linear-gradient(135deg, #165B33 0%, #2E8B57 100%);
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: 0 4px 12px rgba(22, 91, 51, 0.4);
    margin-top: 1rem;
}

.submit-color-button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(22, 91, 51, 0.6);
}

.submit-color-button:active {
    transform: translateY(0);
}

.color-code {
    font-family: 'Courier New', monospace;
    font-size: 1.1rem;
    font-weight: 600;
    color: #ffffff;
    background: rgba(255, 255, 255, 0.1);
    padding: 0.5rem 1rem;
    border-radius: 8px;
    margin: 0;
}

.accuracy {
    font-size: 1.3rem;
    font-weight: 700;
    color: #4CAF50;
    margin: 0.5rem 0;
}

.results-message {
    text-align: center;
    margin-top: 2rem;
    padding: 1.5rem;
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.05);
}

.results-message h3 {
    font-size: 2rem;
    font-weight: 700;
    margin: 0;
}

.excellent-msg {
    color: #4CAF50;
    animation: celebrate 0.6s ease-in-out;
}

.good-msg {
    color: #8BC34A;
}

.okay-msg {
    color: #FFC107;
}

.poor-msg {
    color: #FF5722;
}

@keyframes celebrate {

    0%,
    100% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.1);
    }
}

/* Responsive design */
@media (max-width: 768px) {
    .color-question-display {
        padding: 0.5rem;
    }

    .color-question-text {
        font-size: 1.5rem;
    }

    .color-display-section {
        gap: 1.5rem;
        flex-direction: column;
    }

    .color-box {
        width: 120px;
        height: 120px;
    }

    .submit-color-button {
        padding: 0.8rem 1.5rem;
        font-size: 1rem;
        width: 100%;
    }

    .color-question-meta {
        gap: 1rem;
        font-size: 1rem;
    }

    .target-color-container h3,
    .color-picker-container h3 {
        font-size: 1.2rem;
    }
}