.color-wheel-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
}

.color-wheel-canvas {
    cursor: crosshair;
    touch-action: none;
    border-radius: 50%;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
    transition: transform 0.2s ease;
}

.color-wheel-canvas:active {
    transform: scale(0.98);
}

@media (max-width: 768px) {
    .color-wheel-container {
        padding: 0.5rem;
    }
}