.image-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 2rem;
    gap: 2rem;
}

.image-display-title {
    font-size: 3rem;
    font-weight: bold;
    text-align: center;
    color: #fff;
    margin: 0;
}

.image-display-image {
    max-width: 80%;
    max-height: 60vh;
    object-fit: contain;
    border-radius: 8px;
}