.wager-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    color: white;
    text-align: center;
    gap: 20px;
    padding: 20px;
}

.wager-display h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
}

.wager-input-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    gap: 20px;
}

.wager-slider {
    width: 100%;
}

.wager-amount-display {
    font-size: 3rem;
    font-weight: bold;
    color: #ffd700;
}

.wager-presets {
    display: flex;
    justify-content: space-between;
    gap: 10px;
}

.wager-presets button {
    flex: 1;
    padding: 10px;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid white;
    color: white;
    border-radius: 8px;
    cursor: pointer;
}

.submit-wager-btn {
    padding: 15px;
    font-size: 1.2rem;
    background-color: #ffd700;
    color: #333;
    border: none;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 20px;
}

.player-list-wager {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 15px;
    width: 100%;
    max-width: 800px;
    margin-top: 30px;
}

.player-wager-item {
    background: rgba(0, 0, 0, 0.5);
    padding: 10px;
    border-radius: 8px;
    font-size: 1.2rem;
}

.player-wager-item.ready {
    background: rgba(46, 204, 113, 0.6);
    border: 2px solid #2ecc71;
}