.multi-select-display {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
    max-width: 800px;
    margin: 0 auto;
}

.multi-select-header {
    text-align: center;
    margin-bottom: 2rem;
    width: 100%;
}

.multi-select-text {
    font-size: 2rem;
    margin-bottom: 0.5rem;
    color: #ffffff;
}

.multi-select-instruction {
    font-size: 1.1rem;
    color: #ffffff;
    font-style: italic;
    margin-bottom: 1rem;
}

.multi-select-meta {
    display: flex;
    justify-content: center;
    gap: 2rem;
    font-size: 1.1rem;
    color: #ffffff;
    flex-wrap: wrap;
}

.multi-select-options {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    margin-bottom: 2rem;
}

.multi-select-option {
    display: flex;
    align-items: center;
    padding: 0.8rem;
    border: 2px solid #000000;
    border-radius: 8px;
    background-color: #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    color: black;
}

.multi-select-option:hover {
    background-color: #f5f5f5;
    border-color: #4CAF50;
}

.multi-select-option.selected {
    background-color: #e3f2fd;
    border-color: #2196F3;
}

.multi-select-option.correct-selected {
    background-color: #c8e6c9;
    border-color: #4CAF50;
}

.multi-select-option.correct-unselected {
    background-color: #fff3e0;
    border-color: #FF9800;
}

.multi-select-option.incorrect-selected {
    background-color: #ffcdd2;
    border-color: #f44336;
}

.multi-select-checkbox {
    width: 24px;
    height: 24px;
    margin-right: 1rem;
    cursor: pointer;
    accent-color: #4CAF50;
}

.multi-select-checkbox:disabled {
    cursor: not-allowed;
}

.multi-select-label {
    font-size: 1.1rem;
    cursor: pointer;
    flex: 1;
}

.submit-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
}

.selection-count {
    font-size: 1.1rem;
    color: #555;
    margin: 0;
}

.submit-multi-select-button {
    padding: 1rem 2rem;
    font-size: 1.2rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.submit-multi-select-button:hover:not(:disabled) {
    background-color: #45a049;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.submit-multi-select-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.results-message {
    width: 100%;
    margin-top: 1rem;
}

.score-breakdown {
    padding: 1.5rem;
    background-color: #f5f5f5;
    border-radius: 8px;
    text-align: center;
}

.score-breakdown h3 {
    margin: 0 0 1rem 0;
    color: #333;
    font-size: 1.5rem;
}

.breakdown-line {
    margin: 0.5rem 0;
    font-size: 1.1rem;
    color: #555;
}

.total-line {
    margin: 1rem 0 0 0;
    font-size: 1.3rem;
    font-weight: bold;
    color: #165B33;
    padding-top: 1rem;
    border-top: 2px solid #ddd;
}

@media (max-width: 768px) {
    .multi-select-display {
        padding: 0.5rem;
    }

    .multi-select-text {
        font-size: 1.5rem;
    }

    .multi-select-instruction {
        font-size: 0.9rem;
    }

    .multi-select-option {
        padding: 0.8rem;
    }

    .multi-select-label {
        font-size: 1rem;
    }

    .submit-multi-select-button {
        width: 100%;
        padding: 0.8rem 1.5rem;
        font-size: 1.1rem;
    }
}