.standing-screen {
    width: 100%;
    max-width: 100%;
    margin: 0;
    padding: 20px 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    box-sizing: border-box;
}

.standing-screen h1 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    font-weight: 800;
    background: linear-gradient(to right, #fff, #e0f2f1);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    flex-shrink: 0;
}

/* List for remaining players - Now a Grid */
.standings-list-container {
    flex: 1;
    overflow-y: auto;
    width: 100%;
    max-width: 100%;
    margin: 0;
    padding: 10px;

    /* Grid Layout */
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 15px;
    align-content: start;
    /* Don't stretch rows */

    /* Scrollbar styling */
    scrollbar-width: thin;
    scrollbar-color: rgba(255, 255, 255, 0.3) transparent;
}

.standings-list-container::-webkit-scrollbar {
    width: 8px;
}

.standings-list-container::-webkit-scrollbar-track {
    background: transparent;
}

.standings-list-container::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 20px;
}

.standing-item {
    display: grid;
    grid-template-columns: 40px 1fr 70px;
    /* Rank | Name | Score */
    align-items: center;
    gap: 10px;
    background: rgba(255, 255, 255, 0.1);
    padding: 12px 15px;
    border-radius: 10px;
    transition: all 0.2s ease;
    backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.1);
    height: 60px;
    /* Fixed height for uniformity */
}

/* Specific styling for Top 3 in the grid */
.standing-item:nth-child(1) {
    background: linear-gradient(135deg, rgba(255, 215, 0, 0.3) 0%, rgba(255, 215, 0, 0.1) 100%);
    border-color: rgba(255, 215, 0, 0.5);
    transform: scale(1.02);
}

.standing-item:nth-child(2) {
    background: linear-gradient(135deg, rgba(192, 192, 192, 0.3) 0%, rgba(192, 192, 192, 0.1) 100%);
    border-color: rgba(192, 192, 192, 0.5);
}

.standing-item:nth-child(3) {
    background: linear-gradient(135deg, rgba(205, 127, 50, 0.3) 0%, rgba(205, 127, 50, 0.1) 100%);
    border-color: rgba(205, 127, 50, 0.5);
}

.standing-item:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.standing-item .rank {
    font-weight: 800;
    font-size: 1.1rem;
    opacity: 0.8;
    text-align: center;
}

.standing-item .player-name {
    text-align: left;
    font-weight: 600;
    font-size: 1.1rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.standing-item .player-score {
    text-align: right;
    font-family: 'Courier New', monospace;
    font-weight: 700;
    font-size: 19px;
    background: rgba(0, 0, 0, 0.2);
    padding: 4px 8px;
    border-radius: 6px;
}

/* Entrance Animations */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.standing-item {
    animation: fadeInUp 0.5s ease-out backwards;
}

/* Stagger list items slightly more optimized for grid */
.standing-item:nth-child(-n+20) {
    animation-delay: calc(0.05s * var(--item-index, 1));
    /* Fallback if var not supported, but we'll leave basic staggering for now */
}

/* Client View Overrides */
.standing-screen.client-view .standings-list-container {
    display: block;
    /* Stack items vertically (though there's only one) */
    width: 100%;
}

.standing-screen.client-view .standing-item {
    width: 100%;
    /* Ensure it takes full width */
    margin: 0 auto;
    /* Center it if there's any max-width */
}

/* Override the "1st place" highlighting for the client view */
.standing-screen.client-view .standing-item:nth-child(1) {
    background: rgba(255, 255, 255, 0.1);
    border-color: rgba(255, 255, 255, 0.1);
    transform: none;
}