.landing-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #165B33 0%, #D42426 100%);
  padding: 20px;
}

.landing-container {
  background: white;
  border-radius: 20px;
  padding: 60px 40px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  max-width: 600px;
  width: 100%;
  text-align: center;
}

.landing-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 10px 0;
  color: #165B33;
  -webkit-background-clip: unset;
  -webkit-text-fill-color: #165B33;
  background-clip: unset;
}

.landing-subtitle {
  font-size: 1.2rem;
  color: #666;
  margin: 0 0 40px 0;
}

.landing-buttons {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.landing-button {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 25px 30px;
  border: none;
  border-radius: 15px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.landing-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.landing-button:active {
  transform: translateY(0);
}

.host-button {
  background: linear-gradient(135deg, #165B33 0%, #2E8B57 100%);
  color: white;
}

.client-button {
  background: linear-gradient(135deg, #D42426 0%, #FF6B6B 100%);
  color: white;
}

.button-icon {
  font-size: 2.5rem;
  flex-shrink: 0;
}

.button-text {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.button-text strong {
  font-size: 1.3rem;
  display: block;
}

.button-text small {
  font-size: 0.9rem;
  opacity: 0.9;
  font-weight: 400;
}