.pachinko-selector {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    min-height: 100vh;
}

.pachinko-question {
    font-size: 1.8rem;
    color: white;
    text-align: center;
    margin-bottom: 20px;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.pachinko-instruction {
    font-size: 1.2rem;
    color: white;
    text-align: center;
    margin-bottom: 30px;
    opacity: 0.95;
}

.slot-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    max-width: 400px;
    width: 100%;
    margin: 0 auto;
}

.slot-button {
    aspect-ratio: 1;
    font-size: 2rem;
    font-weight: bold;
    border: 3px solid white;
    border-radius: 15px;
    background: rgba(255, 255, 255, 0.2);
    color: white;
    cursor: pointer;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    min-height: 80px;
}

.slot-button:hover:not(.disabled) {
    background: rgba(255, 255, 255, 0.3);
    transform: scale(1.05);
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
}

.slot-button:active:not(.disabled) {
    transform: scale(0.95);
}

.slot-button.selected {
    background: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);
    border-color: #f5576c;
    box-shadow: 0 5px 25px rgba(245, 87, 108, 0.5);
    animation: pulse 1.5s infinite;
}

.slot-button.disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

@keyframes pulse {

    0%,
    100% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.05);
    }
}

.submission-confirmation {
    margin-top: 30px;
    text-align: center;
    color: white;
    animation: fadeIn 0.5s ease;
}

.submission-confirmation p {
    font-size: 1.3rem;
    margin: 10px 0;
}

.waiting-text {
    font-size: 1rem;
    opacity: 0.8;
    animation: blink 2s infinite;
}

@keyframes blink {

    0%,
    100% {
        opacity: 0.8;
    }

    50% {
        opacity: 0.4;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.timer-warning {
    margin-top: 20px;
    font-size: 1.1rem;
    color: #f39c12;
    text-align: center;
    font-weight: bold;
    animation: blink 1s infinite;
}

.your-selection {
    margin-top: 30px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    backdrop-filter: blur(10px);
    text-align: center;
}

.your-selection p {
    font-size: 1.3rem;
    color: white;
    margin: 0;
}

.your-selection strong {
    font-size: 1.8rem;
}

@media (max-width: 768px) {
    .pachinko-selector {
        padding: 15px;
    }

    .pachinko-question {
        font-size: 1.5rem;
    }

    .pachinko-instruction {
        font-size: 1rem;
        margin-bottom: 20px;
    }

    .slot-grid {
        gap: 10px;
    }

    .slot-button {
        font-size: 1.5rem;
        min-height: 60px;
        border-width: 2px;
        border-radius: 10px;
    }
}